@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Raleway";
  font-display: swap;
  src: local("Raleway Reguar"), local("Raleway-Regular"), url("../fonts/Raleway-Regular.woff2") format("woff2"), url("../fonts/Raleway-Regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Raleway";
  font-display: swap;
  src: local("Raleway Bold"), local("Raleway-Bold"), url("../fonts/Raleway-Bold.woff2") format("woff2"), url("../fonts/Raleway-Bold.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Raleway";
  font-display: swap;
  src: local("Raleway ExtraBold"), local("Raleway-ExtraBold"), url("../fonts/Raleway-ExtraBold.woff2") format("woff2"), url("../fonts/Raleway-ExtraBold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  height: 100%; }

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Raleway", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000; }

main {
  flex: 1 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.hidden {
  display: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.accordion {
  margin: 0;
  padding: 0;
  list-style: none;
  max-width: 680px; }

.accordion__tab {
  min-height: 115px;
  position: relative;
  padding-left: 100px; }
  @media (max-width: 767px) {
    .accordion__tab {
      padding-left: 80px; } }
  .accordion__tab--opened {
    padding-bottom: 38px; }
    @media (max-width: 1023px) {
      .accordion__tab--opened {
        padding-bottom: 15px; } }
    @media (max-width: 767px) {
      .accordion__tab--opened {
        padding-bottom: 35px; } }
    .accordion__tab--opened .accordion__tab-toggle {
      border: none;
      background-image: linear-gradient(180deg, #e41579 0%, #ff3f84 100%); }
      .accordion__tab--opened .accordion__tab-toggle svg {
        fill: #ffffff;
        transform: rotate(45deg); }

.accordion__tab-title {
  padding-top: 25px;
  padding-bottom: 25px; }
  @media (max-width: 767px) {
    .accordion__tab-title {
      display: flex;
      align-items: center;
      min-height: 70px;
      padding-top: 10px;
      padding-bottom: 10px; } }
  .accordion__tab-title h2 {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 16px;
    line-height: 19px; }

.accordion__tab-toggle {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  color: inherit;
  appearance: none;
  transition-property: background;
  transition-duration: 0.3s;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  border: 1px solid #e41579;
  border-radius: 50%; }
  @media (max-width: 767px) {
    .accordion__tab-toggle {
      width: 60px;
      height: 60px; } }
  .accordion__tab-toggle svg {
    transition-property: transform, filter;
    transition-duration: 0.3s;
    fill: #e41579; }
    @media (max-width: 767px) {
      .accordion__tab-toggle svg {
        width: 24px;
        height: 24px; } }
  .accordion__tab-toggle:hover svg,
  .accordion__tab-toggle:focus svg {
    filter: drop-shadow(2px 2px 5px rgba(0, 0, 0, 0.6)); }

.accordion__tab-content {
  transition-property: height;
  transition-duration: 0.3s;
  height: 0;
  overflow: hidden; }
  .accordion__tab-content p {
    margin-top: 0;
    margin-bottom: 12px;
    font-feature-settings: "pnum" on, "lnum" on; }
    @media (max-width: 767px) {
      .accordion__tab-content p {
        font-size: 14px;
        line-height: 24px; } }
  .accordion__tab-content strong {
    font-weight: 700;
    color: #ff3f84; }

.accordion-page {
  padding-top: 150px;
  padding-bottom: 150px;
  overflow: hidden; }
  @media (max-width: 1023px) {
    .accordion-page {
      padding-top: 170px; } }
  @media (max-width: 767px) {
    .accordion-page {
      padding-top: 200px; } }
  .accordion-page h1 {
    display: flex;
    flex-direction: column;
    max-width: 1170px;
    margin-top: 0;
    margin-bottom: 76px;
    font-size: 30px;
    line-height: 35px;
    text-transform: uppercase;
    text-align: center; }
    @media (max-width: 1023px) {
      .accordion-page h1 {
        margin-bottom: 46px;
        font-size: 20px;
        line-height: 23px; } }
    .accordion-page h1 .size-xxl {
      margin-bottom: 12px;
      font-weight: 800;
      font-size: 130px;
      line-height: 153px;
      color: #f6f6f6; }
      @media (max-width: 1023px) {
        .accordion-page h1 .size-xxl {
          margin-bottom: 30px;
          font-size: 80px;
          line-height: 94px; } }
      @media (max-width: 767px) {
        .accordion-page h1 .size-xxl {
          margin-bottom: 20px;
          font-size: 30px;
          line-height: 35px; } }
  .accordion-page .container {
    position: relative; }

.accordion-page__bg {
  position: absolute;
  top: 275px;
  left: 755px;
  width: 556px;
  height: 656px; }
  @media (max-width: 1023px) {
    .accordion-page__bg {
      top: -132px;
      left: calc(50% + 133px);
      width: 214px;
      height: 242px; } }
  @media (max-width: 767px) {
    .accordion-page__bg {
      top: -180px;
      left: auto;
      right: 20px;
      width: 162px;
      height: 199px;
      transform: rotate(60deg); } }
  .accordion-page__bg img {
    display: block;
    width: 100%;
    height: auto; }

.container {
  max-width: 1538px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 50px;
  padding-right: 50px; }
  @media (max-width: 1023px) {
    .container {
      padding-left: 10px;
      padding-right: 10px; } }
